
@media only screen and (min-width: 750px) {
#notice .row>div:nth-child(1) {
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
}
}
.BlockView {
	height: 130px;
	margin: 5px 0 0 5px;
	cursor: pointer;
}
.BlockViewParent {
	padding-left: 0;
	padding-right: 5px;
}
.BlockView div {
	padding: 10px;
	color: white;
	font-size: .9em;
	height: 115px;
	background-repeat: no-repeat;
	background-size: 3em;
	background-position: bottom;
}



